body,div,dl,dt,dd,ul,ol,li,h1,h2,h3,h4,h5,form,fieldset,p,button,pre,strong,code{margin:0;padding:0;color: #333;font-family: 'Noto Sans KR', sans-serif;font-size: 16px;line-height: 150%;font-weight:normal;}
td,th{padding: 0;font-weight:normal;font-size: 12px;color: #333;font-family: 'Noto Sans KR', sans-serif;}
a{color: inherit;font-size: inherit;font-weight: inherit;line-height: inherit;}
body{height:100%;position:relative;overflow-y: scroll;word-break:keep-all;*word-break:keep-all;-ms-word-break:keep-all}
img,fieldset,iframe{border:0 none}
li{list-style:none}
input,select,button,textarea{padding: 0;border: 1px solid #ddd;box-sizing: border-box;}
img{max-width: 100%;max-height: 100%;vertical-align:top}
em,address{font-style:normal}
label,button{cursor:pointer}
button{margin:0;padding:0;vertical-align: top;}
a{text-decoration:none}
button img{*left:auto;}
button{background:none;border:none;}
html:first-child select{height:20px;padding-right:6px}
option{padding-right:6px}
legend{*width:0}
table{width: 100%;border-collapse:collapse;border-spacing:0}
pre{white-space: pre-wrap;}
span{line-height: 100%;}
strong{font-weight: 600;color: #666;}
strong.code{display: inline-block;padding: 2px 4px;font-size: 90%;color: #c7254e;background-color: #f9f2f4;font-family: Menlo,Monaco,Consolas,"Courier New",monospace;vertical-align: middle;}
code{font-size: 12px;color: #666;}

.hljs-attr{color: rgb(25, 144, 184);}

*{-webkit-box-sizing: border-box;-moz-box-sizing: border-box;box-sizing: border-box;}

ol>li{color: #555;}
header{position: absolute;}
header .hd_wrap{overflow-y: auto;position: fixed;z-index: 10;top: 0;left: 0;height: 100%;-webkit-box-sizing: border-box;box-sizing: border-box;}
header .hd_wrap::after{content: "";position: fixed;top: 0;left: 0;display: block;width: 250px;height: 100%;background-color: #eee;}
header .hd_cont{position: relative;z-index: 10;width: 250px;min-width: 220px;padding: 20px;}
header .gnb_btn{position: fixed;z-index: 2000;left: 0;top: 0;display: none;width: 35px;height: 35px;background-color: #c22;}
header h1{margin: 20px 0 40px;font-size: 18px;text-align: center;font-weight: 600;line-height: 130%;color: #cc2222;}
header h1 svg{display: block;margin: 0 auto;}
header nav>ul>li{margin: 10px 0;font-size: 16px;}
header nav>ul>li>a>svg{margin-right: 5px;margin-bottom: 3px;vertical-align: middle;}
header nav>ul>li>ul{padding: 5px 10px;}
header nav>ul>li>ul li{margin: 5px 0;font-size: 14px;}
header nav>ul>li>ul li::before{content: "- ";}
header nav>ul>li>ul li a{color: #555;}
#content{position: relative;z-index: 100;width: calc(100% - 340px);padding: 50px 0;margin-left: 320px;}
#content h2{font-size: 24px;font-weight: bold;margin-bottom: 20px;}
#content h3{font-size: 20px;font-weight: bold;margin-bottom: 10px;}
#content h4{font-size: 16px;font-weight: bold;margin-bottom: 10px;}
#content p{font-size: 16px;line-height: 180%;font-weight: 100;color: #555;}
#content pre{overflow-y: auto;max-height: 500px;}


