.de_btn {
  display: inline-block;
  line-height: 120%;
  text-align: center;
  vertical-align: middle;
  color: #666;
  transition: all 0.5s;
  -webkit-transition: all 0.5s;
  -webkit-background-clip: border-box;
  background-clip: border-box; }

.de_btn:disabled {
  background-color: #eee !important;
  color: #ccc !important;
  cursor: auto; }

.de_btn:focus {
  outline: none; }

/* btn size*/
