.de_rdo, .de_cks {
  display: inline-block;
  vertical-align: middle; }
  .de_rdo i, .de_cks i {
    display: inline-block;
    border: 1px solid #9b9b9b;
    vertical-align: middle;
    background-color: #fff; }
  .de_rdo input, .de_cks input {
    display: block;
    position: absolute;
    width: 0;
    height: 0;
    overflow: hidden;
    opacity: 0; }
  .de_rdo i::after, .de_cks i::after {
    content: "";
    display: block;
    width: 100%;
    height: 100%;
    background-color: transparent;
    transition: all 0.5s;
    -webkit-transition: all 0.5s;
    transform: scale(1, 1);
    -webkit-transform: scale(1, 1);
    transform: scale(1, 1); }
  .de_rdo input:checked ~ i::after, .de_cks input:checked ~ i::after {
    background-color: #1b76ff;
    transform: scale(0.5, 0.5) rotate(0.3deg);
    -webkit-transform: scale(0.5, 0.5) rotate(0.3deg);
    transform: scale(0.5, 0.5) rotate(0.3deg); }
  .de_rdo input:disabled ~ i, .de_cks input:disabled ~ i {
    background-color: #eee; }

.de_rdo.txt i, .de_cks.txt i, .de_cki.txt i {
  margin-right: 10px; }

.de_rdo i {
  -webkit-border-radius: 50%;
  -moz-border-radius: 50%;
  border-radius: 50%; }

.de_rdo i::after {
  -webkit-border-radius: 50%;
  -moz-border-radius: 50%;
  border-radius: 50%; }

.de_cki, .de_cks {
  display: inline-block;
  vertical-align: middle; }
  .de_cki i, .de_cks i {
    display: inline-block;
    border: 1px solid #9b9b9b;
    vertical-align: middle; }

.de_rdo.sg i, .de_cki.sg i, .de_cks.sg i {
  width: 30px;
  height: 30px; }

.de_rdo.ss i, .de_cki.ss i, .de_cks.ss i {
  width: 20px;
  height: 20px; }

.de_cki {
  display: inline-block;
  vertical-align: middle; }
  .de_cki input {
    display: block;
    position: absolute;
    width: 0;
    height: 0;
    overflow: hidden;
    opacity: 0; }
  .de_cki i {
    position: relative; }
  .de_cki i::after {
    content: "";
    display: block;
    position: absolute;
    left: 50%;
    top: 50%;
    width: 5px;
    height: 10px;
    margin-top: -7px;
    margin-left: -4px;
    border: solid #1b76ff;
    border-width: 0 2px 2px 0;
    transition: all 0.5s;
    -webkit-transition: all 0.5s;
    opacity: 0;
    -webkit-transform: rotate(45deg);
    -ms-transform: rotate(45deg);
    transform: rotate(45deg); }
  .de_cki input:checked ~ i::after {
    opacity: 1; }
  .de_cki input:disabled ~ i {
    background-color: #eee; }

